import React from 'react';
import SimpleReactValidator from 'simple-react-validator';
import ClipLoader from 'react-spinners/ClipLoader';
import {Breadcrumbs} from '@material-ui/core';
import {Link} from 'react-router-dom';


import phone from '../../components/assets/images/phone.jpg';
import carti1 from '../../components/assets/images/carti_1.jpg';
import carti2 from '../../components/assets/images/carti_2.jpg';
import magazin_1 from '../../components/assets/images/magazin_1.jpg';
import magazin_2 from '../../components/assets/images/magazin_3.jpg';
import magazin_3 from '../../components/assets/images/magazin_4.jpg';



import './BuyAdv.css';
class BuyAdv extends React.Component
{
    state = {
        isLoading: false,
        contact: {
            name: '',
            phone: '',
            message: '',
            status: ''
        }
    };

    validator = new SimpleReactValidator();

    handleNameChange = (event) =>
    {
        var newState = Object.assign({}, this.state);
        newState.contact.name = event.target.value;
        this.setState(newState);
    }

    handlePhoneChange = (event) =>
    {
        var newState = Object.assign({}, this.state);
        newState.contact.phone = event.target.value;
        this.setState(newState);
    }

    handleMessageChange = (event) =>
    {
        var newState = Object.assign({}, this.state);
        newState.contact.message = event.target.value;
        this.setState(newState);
    }

    handleSubmitMessage = (event) =>
    {
        event.preventDefault();
        if(!this.validator.allValid()) {
            this.validator.showMessages();
            this.forceUpdate();
        } else {
            this.sendMessage();
        }
    }

    sendMessage = () =>
    {
        var message = {
            name: this.state.contact.name,
            phone: this.state.contact.phone,
            message: this.state.contact.message
        };
        const csrf = document.querySelector("meta[name='csrf-token']").getAttribute("content");

        var _this = this;
        this.setState(Object.assign({}, this.state, {isLoading: true}));
        fetch('/messages',{
            method: 'POST',
            body: JSON.stringify(message),
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'X-CSRF-Token': csrf
            }
        })
        .then(response => response.json())
        .then(function(data){
            let newState = Object.assign({}, _this.state);
            newState.isLoading = false;
            newState.contact.status = 'Your message has reached us. We will contact you soon.';
            _this.setState(newState);
        })
        .catch(error=> {
            let newState = Object.assign({}, _this.state);
            newState.isLoading = false;
            newState.contact.status = 'Message delivery failed. Please try to contact us by phone.';
            _this.setState(newState);
        });
    }

    render()
    {
        return(
            <div className="container buy_adv_wrapper">
                 <Breadcrumbs aria-label="breadcrumb">
                    <Link to="/">Home</Link>
                    <Link to={this.props.match.path}>Cumparam</Link>
                </Breadcrumbs>
                <div className="row">
                    <div className="col-lg">
                        <div className="d-flex justify-content-center">
                            <span className="title">Anticariat Online </span> <span className="emph_title">cumpara carti</span><br/>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-sm-12">
                        <div className="d-flex justify-content-center">
                            <span>
                                <img src={phone} alt="anticariat"/>
                            </span>
                            <span className="call_us">Suna-ne acum:</span><span className="call_us_phone">033 303 13 13</span>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-md">
                        <span>
                        Trimite-ne email la adresa contact@targulcartii.ro sau completeaza formularul alaturat cu lista titlurilor de cri pe care vrei s le vinzi i TargulCartii.ro le va cumpra.
                        Singurul principiu pe care se bazeaz funcionarea unui anticariat este vnzarea de carte veche, de la deintorul original ctre cei care i doresc respectiva carte care nu se mai gsete n librrii. Pe acest lucru se bazeaz i anticariatul Trgul Crii, a crui activitate principal este cea de achiziie de carte veche, pe care apoi s o punem spre vnzare n anticariatul nostru online.
                        Biblioteca ta este plin i i este greu s faci loc crilor noi? Te-ai gndit c o soluie poate fi s vinzi o parte din crile pe care deja le-ai citit?
                        </span>
                    </div>
                    <div className="col-md">
                        <form className="contact_form">
                            <div>
                                <input type="text" name="name" placeholder="Nume*" value={this.state.contact.name} onChange={this.handleNameChange.bind(this)}/>
                                <span>{this.validator.message('name', this.state.contact.name, 'required', { className: 'text-danger'})}</span>
                            </div>
                            <div>
                                <input type="text" name="phone" placeholder="Telefon*" value={this.state.contact.phone} onChange={this.handlePhoneChange.bind(this)}/>
                                <span>{this.validator.message('phone', this.state.contact.phone, 'required', { className: 'text-danger'})}</span>
                            </div>
                            <div>
                                <textarea name="message" placeholder="Mesaj" value={this.state.contact.message} onChange={this.handleMessageChange.bind(this)}></textarea>
                                <span>{this.validator.message('message', this.state.contact.message, 'required', { className: 'text-danger'})}</span>
                            </div>
                            <div className="terms_and_conditions">
                                <input type="checkbox"/>
                                <span className="have_read">Am citit si sunt de acord cu Termenii si conditiile / Prelucrarea datelor cu caracter personal (GDPR)</span>
                                <span className="star">*</span>
                            </div>
                            <div className="button_wrapper">
                                <button onClick={this.handleSubmitMessage.bind(this)}>Trimite</button>
                                <ClipLoader className="clip_loader" size={25} loading={this.state.isLoading}/>
                            </div>
                            <div>
                                <span className="server_status ">{this.state.contact.status}</span>
                            </div>
                          </form>
                    </div>
                </div>

                <div className="row">
                    <div className="col-sm-4">
                        <div className="inline_wrapper">
                            <div className="d-flex justify-content-center">
                                <p>Dac i-ai pus problema</p>
                            </div>
                            <div className="d-flex justify-content-center">
                                <p>"cine cumpr cri vechi",</p>
                            </div>
                            <div className="d-flex justify-content-center">
                                <p>Anticariatul Trgul Crii te poate ajuta,</p>
                            </div>
                            <div className="d-flex justify-content-center">
                                <p>una din activitile noastre principale fiind</p>
                            </div>
                            <div className="d-flex justify-content-center">
                                <p>achiziia de cri vechi.</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg">
                        <div className="inline_wrapper">
                           <img src={carti1} alt="anticarita"/>
                        </div>
                        <div className="inline_wrapper">
                            <img src={carti2} alt="anticarita"/>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-xl">
                        <hr/>
                            <p className="p_title">Cine cumpr cri second hand</p>
                            <p>
                                Cumprm carte second hand pentru anticariatul Trgul Crii evalund, de fiecare dat, coleciile de carte veche sau ediiile de carte rar la preul lor corect.  
                                Trgul Crii cumpr cri second hand n cazul n care clienii notri doresc lichidarea bibliotecii i a coleciilor de carte veche pe care le dein.
                                Avem o experien foarte vast n ceea ce privete evaluarea crilor de anticariat i lichidarea stocurilor de cri vechi, iar faptul c clienii ne devin fideli atunci cnd vine vorba de vnzare de carte veche este cartea noastr de vizit.
                            </p>
                        <hr/>
                    </div>
                </div>

                <div className="row">
                    <div className="col-xl">
                            <p className="p_title">Vnzare cri</p>
                            <p>
                                De ce s nu creezi un "circuit" al crilor n biblioteca ta? Banii pe care i poi obine din vnzarea de cri i poi investi mai departe n cri noi. Astfel, plcerea cititului poate s nu mai fie i un efort financiar.
                                Odat ce ai terminat de citit o carte, i vei gsi un loc n biblioteca ta i cel mai probabil nu o vei mai deschide. Dar prin vnzarea de cri vechi, poi face loc n bibliotec noilor cri achiziionate,
                                iar ali iubitori de lectur ca i tine vor profita de crile tale vechi n anticariatul Trgul Crii.
                                Acest lucru i se poate ntmpla cu multe din coleciile pe care le acumulezi, cine nu ar avea de vnzare manuale colare pe care le-a folosit pe parcursul colii i care acum nu i mai sunt utile? 
                                De crile vechi din biblioteca ta se poate bucura un copil chiar n prima lui zi de coal.
                            </p>
                        <hr/>
                    </div>
                </div>

                <div className="row">
                    <div className="col-xl">
                            <p className="p_title">Cumprm cri vechi de la domiciului clientului</p>
                            <p>
                                n funcie de numrul volumelor pe care doreti s le vinzi, noi ne deplasm la domiciliul tu gratis pentru evaluarea i cumprarea crilor vechi.
                                Apelnd la Trgul Crii pentru vnzare de cri vechi poi fi sigur de promptitudinea i seriozitatea noastr, iar plata o efectum ntotdeauna pe loc.
                                Cea mai bun metod este s ai o viziune de ansamblu asupra crilor din bibliotec pe care doreti s le vinzi, s ne-o transmii, iar discuia va decurge n mod natural de aici. Noi te putem ajuta cu sfaturi legate de vnzarea de cri second hand oricnd ai nevoie.
                            </p>
                            <div>
                                <img src={magazin_1} alt="anticariat" className="shop_img"/>
                                <img src={magazin_2} alt="anticariat" className="shop_img"/>
                                <img src={magazin_3} alt="anticariat" className="shop_img"/>
                                <div className="magazin_p_wrapper">
                                    <p className="par">
                                        Pentru un numr mic de cri<br/>
                                        te invitm la sediul nostru din<br/>
                                        <span className="red">&nbsp;&nbsp;&nbsp;Str. Doamnei, nr. 27-29,</span><br/>
                                        <span>&nbsp;&nbsp;&nbsp;Sector 3, Bucureti.</span>
                                    </p>
                                </div>
                            </div>
                        <hr/>
                    </div>
                </div>
        </div>);
    }
}


export default BuyAdv;